@import "helpers/_variables";
@import "helpers/_mixins";

div[class="packageInfo"] {
    position: fixed;
    top: 50%;
    left: 50%;
    background: darken($background, 5%);
    color: $header-text;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
    box-sizing: border-box;
    border-radius: 3px;
    width: 550px;
    padding: 1.5rem;
    display: block;
    z-index: 3;
    line-height: 1.3;
    @include mobile {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        width: 100%;
        font-size: 15px; }
    header[class="package-info-header"] {
        display: flex;
        justify-content: space-between;
        align-items: center;
        button {
            background: none;
            border: none;
            padding: 0.3rem;
            cursor: pointer;
            svg {
                fill: $header-text; }
            &:hover svg {
                fill: darken($header-text, 5%); } } }
    button[class="download-btn"] {
        @include button;
        padding: 0.4rem;
        font-size: 1.05em; }
    section[class="changelog"] {
        background: $text-color;
        color: #0f0f0f;
        padding: 1.5rem;
        box-sizing: border-box;
        pre {
            white-space: pre-wrap; } } }

.close {
    display: none; }

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0,0,0, 0.5);
    z-index: 2;
    display: block; }
